/* Estilos base (móvil primero) */
table.propiedades {
  margin-top: 4rem;
  width: 100%;
}
table.propiedades thead {
  display: none;
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades tr {
  display: block;
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
}
table.propiedades td {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  text-align: right;
  border-bottom: 1px solid #eee;
}
table.propiedades td:last-child {
  border-bottom: none;
}
table.propiedades td::before {
  content: attr(data-label);
  font-weight: bold;
  margin-right: 1rem;
  text-align: left;
}
table.propiedades .imagen-tabla, table.propiedades .propiedades img {
  width: 60px;
  height: auto;
  display: inline-block;
  margin: 0 auto;
}
table.propiedades .input-cantidad {
  width: 60px;
}
table.propiedades .boton-rojo-block {
  padding: 0.3rem 0.6rem;
}

/* Estilos para desktop */
@media (min-width: 768px) {
  table.propiedades {
    border-spacing: 0;
    text-align: center;
    border-collapse: collapse;
  }
  table.propiedades thead {
    display: table-header-group;
  }
  table.propiedades tr {
    display: table-row;
    margin-bottom: 0;
    border: none;
    border-radius: 0;
  }
  table.propiedades td {
    display: table-cell;
    padding: 1rem;
    text-align: center;
    border-bottom: none;
  }
  table.propiedades td::before {
    content: none;
  }
  table.propiedades .imagen-tabla, table.propiedades .propiedades img {
    width: 100px;
  }
  table.propiedades .input-cantidad {
    width: auto;
  }
}
